<template>
  <div id="login" style="width:450px; display:inline-block;">
    <br></br>
      <h1>ыбЬъ╖╕ьЭ╕</h1>
    <br></br>
      <v-form v-model="valid" ref="form">
        <v-text-field label="ьХДьЭ┤ыФФ (8~14ьЮРыжм(ьШБым╕,ьИльЮР))" v-model="id" :rules="idRules" :counter="14"></v-text-field>
        <v-text-field label="ы╣Ды░Аы▓ИэШ╕ (8~14ьЮРыжм(ьШБым╕,ьИльЮР,эК╣ьИШым╕ьЮР))" type="password" v-model="pw" :rules="pwRules" :counter="14"></v-text-field>
        <v-btn @click="login" :disabled="!valid">эЩХьЭ╕</v-btn>
      </v-form>
    <pre>{{$data}}</pre>
  </div>
</template>

<script>
  export default {
    data: () => ({
      valid:false,
      id:'',
      idRules: [
        v => !!v || 'ьХДьЭ┤ыФФые╝ ьЮСьД▒эХ┤ьг╝ьД╕ьЪФ.',
        v => v.length <= 14 || '8~14ьЮРыжмые╝ ьЮЕыаеэХ┤ьг╝ьД╕ьЪФ.'
      ],
      pw:'',
      pwRules: [
        v => !!v || 'ы╣Ды░Аы▓ИэШ╕ые╝ ьЮСьД▒эХ┤ьг╝ьД╕ьЪФ.',
        v => v.length <= 14 || '8~14ьЮРыжмые╝ ьЮЕыаеэХ┤ьг╝ьД╕ьЪФ.'
      ],
      lat:'127.0352915',
      lng:'37.5360206',
      dong:'ьЧньВ╝ыПЩ'
    }),
    methods: {
      login () { // эБ┤ыжн : ыбЬъ╖╕ьЭ╕ ьЩДыгМ
        if(this.$refs.form.validate()) {
          this.$axios.post('/api/course/topten', {
            id:this.id,
            pw:this.pw,
            lat:this.lat,
            lng:this.lng,
            dong:this.dong
          }) .then((response) => {

              console.log('login')
              this.result = response.data
              console.log(this.result)

              // if (ыбЬъ╖╕ьЭ╕ ыРШьЧИьЭД ыХМьЭШ result){
              //   this.$router.push('main')
              // }else{
              //    ьШдыеШыйФьД╕ьзА ь╢Ьыае
              // }
          })
        }
      }
    }
  }
</script>

