<template>
  <div id="join" style="width:450px; display:inline-block;" data-app>
    <br></br>
      <h1>эЪМьЫРъ░АьЮЕ</h1>
    <br></br>
      <v-form v-model="valid" ref="form">
        <v-text-field label="ьЭ┤ыйФьЭ╝ (example@gmail.com)" v-model="email" :rules="emailRules"></v-text-field>
        <v-text-field label="ьХДьЭ┤ыФФ (8~14ьЮРыжм(ьШБым╕,ьИльЮР))" ref= "id" v-model="id" :rules="idRules" :counter="14"></v-text-field>
        <v-btn outline @click="checkid">ьдСы│╡ь▓┤эБм</v-btn>
        <v-text-field label="ыЛЙыДдьЮД (2~8ьЮРыжм(ьШБым╕ OR эХЬъ╕А))" v-model="nickname" :rules="nicknameRules" :counter="8"></v-text-field>
        <v-text-field label="ы╣Ды░Аы▓ИэШ╕ (8~14ьЮРыжм(ьШБым╕,ьИльЮР,эК╣ьИШым╕ьЮР))" type="password" v-model="pw" :rules="pwRules" :counter="14"></v-text-field>
        <v-text-field label="ы╣Ды░Аы▓ИэШ╕ эЩХьЭ╕" v-model="validpw" :rules="pwConfirmRule"></v-text-field>
        <v-slider label="ыкЗ ыЕДьГЭ ьДаэГЭ (ыВШьЭ┤)" min="1930" max="2018" track-color="black" thumb-label thumb-color="black" v-model="age" :value="age" :rules="ageRules"></v-slider>
        <v-btn @click="join" :disabled="!valid">эЩХьЭ╕</v-btn>
      </v-form>
    <pre>{{$data}}</pre>
  </div>
</template>


<script>
  export default {
    data: () => ({
         valid:false,
         email:'',
         emailRules: [
           v => !!v || 'ьЭ┤ыйФьЭ╝ьЭД ьЮСьД▒эХ┤ьг╝ьД╕ьЪФ.',
           v => /^\w+([.-]?\w+)*@\w+([.-]?\w+)*(\.\w{2,3})+$/.test(v) || 'ьЭ┤ыйФьЭ╝эШХьЛЭьЧР ызЮъ▓М ьЮЕыаеэХ┤ьг╝ьД╕ьЪФ.'
         ],
         id:'',
         idRules: [
            v => !!v || 'ьХДьЭ┤ыФФые╝ ьЮСьД▒эХ┤ьг╝ьД╕ьЪФ.',
            v => v.length <= 14 || '8~14ьЮРыжмые╝ ьЮЕыаеэХ┤ьг╝ьД╕ьЪФ.'
         ],
         nickname:'',
         nicknameRules: [
           v => !!v || 'ыЛЙыДдьЮДьЭД ьЮСьД▒эХ┤ьг╝ьД╕ьЪФ.',
           v => v.length >= 3 || 'ыЛЙыДдьЮДьЭ┤ ыДИым┤ ьззьК╡ыЛИыЛд.',
           v => v.length <= 8 || '2~8ьЮРыжмые╝ ьЮЕыаеэХ┤ьг╝ьД╕ьЪФ.',
           v => /^[a-zA-Z_ъ░А-эЮгуД▒-уЕОуЕП-уЕг]{2,8}$/.test(v) || 'эК╣ьИШым╕ьЮРыКФ ьВмьЪйэХа ьИШ ьЧЖьК╡ыЛИыЛд.'
         ],
         pw:'',
         pwRules: [
           v => !!v || 'ы╣Ды░Аы▓ИэШ╕ые╝ ьЮСьД▒эХ┤ьг╝ьД╕ьЪФ.',
           v => v.length <= 14 || '8~14ьЮРыжмые╝ ьЮЕыаеэХ┤ьг╝ьД╕ьЪФ.',
           v => /\W/.test(v) || 'эК╣ьИШым╕ьЮРъ░А эХШыВШьЭ┤ьГБ эПмэХиыРШьЦ┤ьХ╝эХйыЛИыЛд.',
           v => /[a-zA-Z]/.test(v) || 'ым╕ьЮРъ░А эХШыВШьЭ┤ьГБ эПмэХиыРШьЦ┤ьХ╝эХйыЛИыЛд.'
         ],
         validpw:'',
         pwConfirmRule: [
           v => !!v || 'ы╣Ды░Аы▓ИэШ╕ые╝ эЩХьЭ╕эХ┤ьг╝ьД╕ьЪФ.',
           v => v == document.forms[0][5]._value || 'ы╣Ды░Аы▓ИэШ╕ъ░А ыЛдыжЕыЛИыЛд.'
         ],
         age:0,
         ageRules: [

         ]
       }),
      methods: {
        join () { // эБ┤ыжн : эЪМьЫРъ░АьЮЕ ьЩДыгМ
          if (this.$refs.form.validate()) {
            this.$axios.post('/api/user/join', {
              email:this.email,
              id:this.id,
              nickname:this.nickname,
              pw:this.pw,
              age:this.age
            })
          }
        },
        checkid () { // ьХДьЭ┤ыФФ ьдСы│╡ь▓┤эБм
          console.log(this.joinidcheck)
          if(this.$refs.id.validate()) {
            this.$axios.post('/api/user/overlap', {
              joinidcheck:this.id
            }).then((response) => {
                console.log('join')
                this.result = response.data
                console.log(this.result)
            })
          }
        }
      }
    }
</script>

<style lang="css">

</style>



